/** SchÃ©ma json d'enregistrement des requetes. */
interface JSearchesDatas {
	searches: JSearchDatas[]
}

interface JSearchDatas {
	id: string
	title: string
	whereXml: string
}


/**
 * Config de l'affichage des rÃ©sultats d'une requete.
 */
interface OSearchResultOptions {
	/** Nb max d'items retournÃ©s. */
	max?: number
	/** Init de la liste des rÃ©sultats. */
	srcGrid?: OSrcGridInit
	/** Actions de la toolbar. */
	barActions?: IAction<SearchResult>[]
	msgs?: JSearchResultL10nMsgs
}

/**
 * Area de rÃ©sultat de recherche SANS persistance
 */
export class SearchResultArea extends SimpleTagArea<ISearchResultCtx, SearchResult> {
	options: OSearchResultOptions;
	conf: OSearchResultInit;
	whereCrit?: Element | ((ctx: IRegPointer<IWspUiEnv>) => Element);

	constructor(id?: string, options?: OSearchResultOptions, init?: OSearchResultInit) {
		super('wsp-search-result', null, id);
		this.conf = init || {};
		this.options = options || {};
		if (!this.options.max) this.options.max = 2000;
	}

	setWhereCrit(whereCrit?: Element | ((ctx: IRegPointer<IWspUiEnv>) => Element)): this {
		this.whereCrit = whereCrit;
		return this;
	}

	getWhereCrit(ctx: IRegPointer<IWspUiEnv>): Element {
		return this.whereCrit instanceof Element ? this.whereCrit : this.whereCrit(ctx);
	}

	protected newElt(ctx: ISearchResultCtx, lastDatas?: JLastDatas): SearchResult {
		const elt = super.newElt(ctx, lastDatas);
		elt.initialize(Object.assign({
			reg: ctx.reg,
			area: this,
			areaContext: ctx,
		}, this.conf));
		return elt;
	}

}

/**
 * Area de rÃ©sultat de recherche AVEC persistance
 */
export class SearchResultPersistArea extends SearchResultArea {
	constructor(protected persist: ISearchPersist, options: OSearchResultOptions) {
		super(persist.id);
		this.options = options;
		if (!this.options.max) this.options.max = 2000;
		this.whereCrit = persist.whereCrit;
	}

	getLabel(ctx: ISearchResultCtx): string {
		const ovrLabel = (typeof this._label === 'function') ? this._label(ctx, this) : this._label;
		return ovrLabel || this.persist.title || "...";
	}

	getPersist(ctx: ISearchResultCtx): ISearchPersist {
		this.persist.whereCrit = this.whereCrit;
		this.persist.title = this.getLabel(ctx);
		return this.persist;
	}
}


export interface ISearchResultCtx extends OUiCritInit, ISearchStore {
}


/**
 * View d'affichage d'une requÃªte.
 * PrÃ©vue pour Ãªtre aussi utilisable en dehors du widget container Searches.
 */
export interface SearchResult extends BaseAreaView<ISearchResultCtx>, ISearchViewResult {
	initialize(init: OSearchResultInit): this
}

export interface OSearchResultInit extends OBaseAreaViewInit<ISearchResultCtx> {
	/** true par dÃ©faut */
	editable?: boolean
}

export interface JSearchResultL10nMsgs {
	/** vars : 'max' */
	resultsMore?: (max: number) => string
	resultsEmpty?: string
	resultsOne?: string
	resultsCount?: (count: number) => string
}

export class SearchResult extends BaseAreaView<ISearchResultCtx> implements ISearchViewResult {
	reg: IReg<IWspUiEnv>;
	area: SearchResultPersistArea | SearchResultArea;
	wsp: Wsp;

	protected _grid: SrcGrid;
	protected _abstract: Button | HTMLDivElement;
	protected _found: HTMLElement;
	protected _bar: BarActions<SearchResult>;

	protected _searchColumns: Map<string, string | ISearchFunc>;

	protected _currentWhere: Element;

	protected _initialize(init: OSearchResultInit) {
		const conf = Object.assign({
			editable: true,
		}, init)
		super._initialize(conf); //init this.reg, this.area, this.areaContext
		this.wsp = this.reg.env.wsp;
		const sr = this.attachShadow(DOMSH.SHADOWDOM_INIT);
		//sr.appendChild(<div>{DOM.ser(this.area.persist.whereCrit)}</div>);
		this.reg.installSkin("scroll/small", sr);
		this._initAndInstallSkin(this.localName, init);
		const head = sr.appendChild(<div id="header"/>);
		this._abstract = head.appendChild(<Button id="abstract" onclick={this.onEdit}/> as Button);
		this._abstract.disabled = !conf.editable;
		this._found = head.appendChild(<span id="found"/>);
		this._bar = head.appendChild(<BarActions id="bar" Ã®={{
			reg: this.reg,
			actionContext: this,
			actions: this.area.options.barActions,
		} as OBarActionsInit<SearchResult>}/> as BarActions<SearchResult>);
		const initGrid = BASIS.newInit(this.area.options.srcGrid, this.reg);
		if (initGrid.skinScroll === undefined) initGrid.skinScroll = 'scroll/small';
		this._grid = sr.appendChild(new SrcGrid().initialize(initGrid));
	}

	protected _refresh() {
		super._refresh();
		if (!DOM.nodeEquals(this._currentWhere, this.area.getWhereCrit(this.areaContext))) {
			this._abstract.textContent = null;
			const descTxt = this.area.getDescription(this.areaContext);
			if (descTxt)
				this._abstract.textContent = descTxt;
			else {
				const desc = UiCritBool.AREA_AND_ROOT.buildAbstract(this.area.getWhereCrit(this.areaContext), this.areaContext);
				if (desc) this._abstract.appendChild(desc);
			}
			this._grid.refresh();// indispensble pour terminer d'initialiser le tree (def des colonnes, ...)
			this._bar.refresh();
			this.doSearch();
		}
	}

	async doSearch() {
		//console.log("doSearch:::", this.area.persist.whereCrit);
		this._grid.srcGridDatas.setDatas([]);
		this._found.textContent = "En cours...";
		const currentWhere = this.buildWhereXml();
		try {
			if (!this._searchColumns) {
				this._searchColumns = new Map();
				this._grid.fillSearchColumns(this._searchColumns);
			}
			const req = new SearchRequest(this._searchColumns).setMax(this.area.options.max + 1).setWhereXml(currentWhere);
			const results = await WSP.fetchSearchJson(this.wsp, this, req.toXml());
			const count = results.length;
			const max = this.area.options.max;
			//console.log("results::::", results);
			this._grid.srcGridDatas.setDatas(results);
			if (results.length === 0) this._found.textContent = this.area.options.msgs?.resultsEmpty ? this.area.options.msgs.resultsEmpty : "Aucun item trouvÃ©";
			else if (results.length === 1) this._found.textContent = this.area.options.msgs?.resultsOne ? this.area.options.msgs.resultsOne : "1 item trouvÃ©";
			else if (results.length > this.area.options.max) this._found.textContent = this.area.options.msgs?.resultsMore ? this.area.options.msgs.resultsMore(max) : `Plus de ${max} items trouvÃ©s`;
			else this._found.textContent = this.area.options.msgs?.resultsCount ? this.area.options.msgs.resultsCount(count) : `${count} items trouvÃ©s`;
			this._currentWhere = currentWhere;
		} catch (e) {
			this._found.textContent = "Ãchec";
			ERROR.log(`Do search failed: ${DOM.ser(currentWhere)}`, e);
		}
	}

	buildWhereXml(): Element {
		return this.area.getWhereCrit(this.areaContext);
	}

	async viewSearch(whereCrit: Element, searchId: string | null, searchLabel?: string, searchDescription?: string): Promise<string> {
		console.warn('not implemented');
		return undefined;
	}

	setWedSearch() {
		const coord = (this.reg.env as IWedSearchCoordinatorPointer).wedSearchCoord;
		if (coord && this._currentWhere) {
			coord.setParamsOnce(UiCritBool.AREA_AND_ROOT.buildWedSearch(this._currentWhere, this.areaContext.criterions) || null);
		}
	}

	onEdit(this: Button) {
		if (this.disabled) return;
		const me = DOMSH.findHost<SearchResult>(this);
		editSearch(me.area, me.areaContext, me, (me.areaContext as Searches).kind);
	}
}

/** */
export class FocusItemSetWedSearch extends FocusItem {

	execute(ctx: SrcGrid, ev?: Event) {
		const searchRes = DOMSH.findHost<SearchResult>(ctx);
		searchRes.setWedSearch();
		super.execute(ctx, ev);
	}
}


