async function editSearch(search: SearchResultPersistArea | SearchResultArea, ctx: ISearchResultCtx, from: HTMLElement, kind: 'item' | 'task' = 'item') {
	const {SearchItems} = await import("back/wsp/dialogs/searchItems.js");
	const {TplRequestsMenu} = await import("back/wsp/actions/searchesActions.js");

	const forcedSearchRequest = search instanceof SearchResultPersistArea ? search.getPersist(ctx) : search.getWhereCrit(ctx);
	if (!forcedSearchRequest)
		if (kind === 'item')
			ctx.reg.addToList("actions:searchItems:bar:item", "tplRequestsMenu", 1, TplRequestsMenu.makeFromSearchPersistLists(ctx, "requests:item", "requests:item:searchItems:item"));
		else
			ctx.reg.addToList("actions:searchItems:bar:task", "tplRequestsMenu", 1, TplRequestsMenu.makeFromSearchPersistLists(ctx, "requests:task", "requests:item:searchItems:task"));

	const ct = new SearchItems().initialize({
		reg: ctx.reg,
		showMainView: true,
		srcGrid: {
			actions: kind === 'item' ?
				ctx.reg.mergeLists("actions:wsp:shortDesc", "actions:searchItem:shortDesc:item")
				: ctx.reg.mergeLists("actions:wsp:shortDesc:task", "actions:searchItem:shortDesc:task"),
			accelKeyMgr: new AccelKeyMgr().initFromMapActions(kind === 'item' ?
				ctx.reg.mergeListsAsMap("accelkeys:searchItem:shortDesc", "accelkeys:wsp:shortDesc:item")
				: ctx.reg.mergeListsAsMap("accelkeys:wsp:shortDesc:task", "accelkeys:wsp:shortDesc:task")),
		},
		criterions: ctx.criterions,
		appHeader: {
			mainActions: ctx.reg.mergeLists(SearchItems.APPBAR_MAIN_ACTIONS, kind === 'item' ? "actions:searchItems:bar:item" : "actions:searchItems:bar:task"),
		} as OAppHeaderInit<any>,
		searchStore: search instanceof SearchResultPersistArea ? ctx : null,
		msgs: kind === 'item' ? undefined : {
			resultsMore: max => `Plus de ${max} tÃ¢ches trouvÃ©es`,
			resultsEmpty: 'Aucune tÃ¢che trouvÃ©e',
			resultsOne: '1 tÃ¢che trouvÃ©e',
			resultsCount: count => `${count} tÃ¢ches trouvÃ©es`,
		},
		canEditTitle: search instanceof SearchResultPersistArea ? true : false,
		initSearchRequest: forcedSearchRequest,
	} as OSearchItemsInit);
	const searchTitle = search.getLabel(ctx);

	POPUP.showDialog(ct, from, {
		titleBar: {
			barLabel: {
				label: kind === 'item'
					? (searchTitle ? `Ãdition de la recherche d'items '${searchTitle}'` : "Ãdition de la recherche d'items")
					: (searchTitle ? `Ãdition de la recherche de tÃ¢ches '${searchTitle}'` : "Ãdition de la recherche de tÃ¢ches")
			}, closeButton: {}
		},
		resizer: {},
		viewPortX: "middle",
		viewPortY: "middle",
		initWidth: "90vw",
		initHeight: "90vh"
	});
}

